import React from "react";
import "./style/Loader.css";

const Loader = () => {
  return (
    <section class="dots-container">
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
    </section>
  );
};

export default Loader;

export const ButtonLoader = () =>{
  return (
    <div className="btn-loader"></div>
  )
}
